.add-playlist {
  display: flex;
  align-items: center;
  width: 100%;
  height: 75px;
  background: 0;
  border: 0;
  border: 1px solid #000;
  font-size: 14px;
  color: #aaaaaa;
  cursor: pointer;

  &:hover {
    color: #fff;
    background: #5c5c5c;
  }

  span{
    margin: 0 10px;
  }
}